/*  Color Scheme  */
/*  Background: #1e1e1e
    Header: #323234
    Tabs: #252527
    Text: #a3aab4
    Strings (Orange): #b87656
    Parentheses (Yellow): #d49d3f
    Comments (Green): #4a7837
    Fragment (Cyan): #58bf99
    Style (Light Blue): #73bed9
    Function (Dark Blue): #3067a7
    Import/Return (Purple): #a8659d
*/

@media (max-width: 700px) {
  .App {
    width: 100% !important;
    margin-left: 0 !important;
  }
}

.App {
  text-align: center;
  min-height: 97vh;
  width: 95%;
  background-color: #1e1e1e;
  color: #a3aab4;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #323234;
  color: #a3aab4;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (max-width:600px){
  img {
    width: 100%;
  }

  #me {
    height: 200px !important;
    width: 200px !important;
  }

  #video {
    width: 100% !important;
  }
}

img {
	max-height: 300px;
}

#me {
  object-fit: cover; 
  height: 300px; 
  width: 300px; 
  border-radius: 50%;
}

#video {
  aspect-ratio: 16/9;
  width: 520px;
}

#logo {
  height: 70px;
}